
import { _decorator, Component, Node, PhysicsSystem2D, EPhysics2DDrawFlags, Collider2D, Contact2DType, IPhysics2DContact, BoxCollider2D, director, Sprite, Color, resources, SpriteFrame, Layers, find, Prefab, instantiate } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PaoPerManeger')
export class PaoPerManeger extends Component {
    
    public bulletArray = [];
    public hasEmeny = false;
    onLoad(){
        // director.enable = true;
        //绘制物理信息
        PhysicsSystem2D.instance.enable = true;
        PhysicsSystem2D.instance.debugDrawFlags = EPhysics2DDrawFlags.Aabb | 
            EPhysics2DDrawFlags.Pair | 
            EPhysics2DDrawFlags.CenterOfMass |
            EPhysics2DDrawFlags.Joint |
            EPhysics2DDrawFlags.Shape ;
    }

    start(){
        
        // 批量生成子弹
        let that = this;
        this.schedule(function(){
            if(this.hasEmeny){
                console.log('createBullet')
                that.createBullet(that);
            }
        },1)
        
        //注册单个碰撞体的回调函数
        console.log('tool information')
        let collider = this.node.getComponent(Collider2D);
        console.log(collider);
        // console.log(collider);
        if (collider) {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
            collider.on(Contact2DType.PRE_SOLVE, this.onPreSolve, this);
            collider.on(Contact2DType.POST_SOLVE, this.onPostSolve, this);
        }

        // 注册全局碰撞回调函数
        // if (PhysicsSystem2D.instance) {
        //     PhysicsSystem2D.instance.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
        //     PhysicsSystem2D.instance.on(Contact2DType.END_CONTACT, this.onEndContact, this);
        //     PhysicsSystem2D.instance.on(Contact2DType.PRE_SOLVE, this.onPreSolve, this);
        //     PhysicsSystem2D.instance.on(Contact2DType.POST_SOLVE, this.onPostSolve, this);
        // }
    }
    
    onBeginContact (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体开始接触时被调用一次
        this.hasEmeny = true;
        console.log('Emeny in my are onBeginContact');

    }
    onEndContact (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体结束接触时被调用一次
        console.log('Work onEndContact');
        this.hasEmeny = false;

    }
    onPreSolve (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 每次将要处理碰撞体接触逻辑时被调用
        console.log('Work onPreSolve');
    }
    onPostSolve (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 每次处理完碰撞体接触逻辑时被调用
        console.log('Work onPostSolve');

    }

    update(deltaTime: number) {
        console.log(this.bulletArray.length);
        for(let i=0;i<this.bulletArray.length;i++){
            let node = this.bulletArray[i];
            let y = node.position.y;
            let x = node.position.x;
            x = x - 500 * deltaTime;
            y = y + 500 * deltaTime;
            node.setPosition(x,y,0)
            //TODO:出屏幕后销毁
        }
    }

    createBullet(that){
        let node = new Node();
        // node.addComponent(Sprite);
        resources.load("perfabs/Bullet_Pre",Prefab,(err,Prefab)=>{
            if(!Prefab){
                console.log('子弹预制体为空')
            }else{
                console.log('预制体制作完成')
                node = instantiate(Prefab);
                if(!node){
                    console.log('没有节点')
                }else{
                    node.layer = Layers.Enum.UI_2D;
                    node.parent = this.node;
                }
                
            }
        })
        // resources.load('zidan1/spriteFrame',SpriteFrame,(_err,sproteFarme)=>{
        //     node.getComponent(Sprite).spriteFrame = sproteFarme;
        //     node.layer = Layers.Enum.UI_2D;
        //     console.log(find("left"))
        //     console.log(this.node)
        //     node.parent = this.node;
        //     console.log(node);
        //     // node.parent = find("left").getChildByName("rightUp").getChildByName("pao")
        // })
        that.bulletArray.push(node);
    }
}




